<template>
  <div id="app">
    <el-row class='head clearfix' :style="{backgroundColor:'rgba(0,0,0,'+opaque+')'}">
      <el-col :span="6"><div class="grid-content bg-purple">
        <h1>
          <router-link to='/home'>CIAO</router-link>
        </h1>
      </div></el-col>
      <el-col :span="18"><div class="grid-content bg-purple-light">
        <ul class='top-nav clearfix'>
          <li v-for='item in items'>
            <router-link :to='item.path'>{{item.value}}</router-link>
          </li>
        </ul>
      </div></el-col>
    </el-row>
    <router-view :data='public'></router-view>
    <div class="cloth" v-if='public.isAlert'></div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      items: {
        Home: {
          value: 'Home',
          path: 'home'
        },
        About: {
          value: 'About',
          path: 'about'
        }
      },
      public: {
        isAlert: false,
        scroll: 0
      },
      opaque: 0
    }
  },
  methods: {
    menu () {
      let top = document.body.scrollTop
      this.public.scroll = top
      top = top > 400 ? 400 : top
      this.opaque = top * 0.002
    }
  },
  mounted () {
    window.addEventListener('scroll', this.menu)
  }
}
</script>

<style>
html,body{
  width:100%;
  height:100%;
  background-color:#F1F1F1;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
body {
  transition:all .3s;
}
body,ul,li,h1,h2,h3,h4,h5,h6,p{
  margin:0;
  padding:0;
  list-style:none;
  color:white;
}
a{
  color:white;
  text-decoration:none;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
.clearfix:after {
  content: "";
  display: block;
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
.clearfix {
  zoom: 1;
}
#app {
  width:100%;
}
#app .head{
  height:60px;
  line-height:60px;
  width:100%;
  position:fixed;
  z-index:99;
}
#app .head a:hover{

}
#app .head h1{
  margin-left:20px;
}
#app .head h1 a{
  text-decoration:underline;
  text-shadow:0 0 5px #000;
  transition: all .3s;
}
#app .head h1 a:hover{
  text-shadow:0 0 20px #000;
}
#app .top-nav li{
  float:left;
  padding: 0px 5px;
  margin:0px 5px;
}
#app .top-nav li a{
  transition: all .3s;
  color:#a9a9a9;
}
#app .top-nav li a.router-link-active{
  color:#fff;
}
#app .top-nav li a:hover{
  color:#fff;
}
.cloth{
  width:100vw;
  height:100vh;
  position:fixed;
  top:0;
  left:0;
  background-color:rgba(0,0,0,.7);
  z-index:100;
}
</style>



