<script>
export default {
  name: 'home',
  data () {
    return {
      load: true,
      domel: {},
      current: 0,
      switch: true,
      isJump: true,
      brand: undefined,
      isVideo1: false,
      isVideo2: false,
      isVideoClass1: false,
      isVideoClass2: false,
      playerOptions1: {
        start: 0,
        playsinline: true,
        muted: false,
        language: 'en',
        autoplay: true,
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        height: 522,
        width: 660,
        sources: [{
          type: 'video/mp4',
          src: '../assets/e697d027f6e43e6f0602f0efaa6fe5e5.mp4'
        }]
      },
      playerOptions2: {
        start: 0,
        playsinline: true,
        muted: false,
        language: 'en',
        autoplay: true,
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        height: 522,
        width: 660,
        sources: [{
          type: 'video/mp4',
          src: '../assets/63ab99df141f890e2e1476f0c3c9c5b7.mp4'
        }]
      },
      CImg1: [
        {path: require('../assets/photo-13.fd84f7e17024575eca478d09e2e27561.jpg'), index: -2},
        {path: require('../assets/photo-11.cdc35198b86f7c3b8274960048d7798e.jpg'), index: 0},
        {path: require('../assets/photo-12.561c36258be06bea29b5369358380477.jpg'), index: 2},
        {path: require('../assets/photo-13.fd84f7e17024575eca478d09e2e27561.jpg'), index: 4},
        {path: require('../assets/photo-11.cdc35198b86f7c3b8274960048d7798e.jpg'), index: 6},
        {path: require('../assets/photo-12.561c36258be06bea29b5369358380477.jpg'), index: 8}
      ],
      CImg2: [
        {path: require('../assets/photo-24.4737d78206419163c86f4bf031f9b433.jpg'), index: -1},
        {path: require('../assets/photo-21.a2097e00a13699322ed807c2bedd1da6.jpg'), index: 1},
        {path: require('../assets/photo-23.6e90ed03800c2ae047d8e5e9567ffcaa.jpg'), index: 3},
        {path: require('../assets/photo-24.4737d78206419163c86f4bf031f9b433.jpg'), index: 5},
        {path: require('../assets/photo-21.a2097e00a13699322ed807c2bedd1da6.jpg'), index: 7},
        {path: require('../assets/photo-23.6e90ed03800c2ae047d8e5e9567ffcaa.jpg'), index: 9}
      ]
    }
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  methods: {
    flag (n) {
      if (this.domel['page1top'] === undefined && n === 1) {
        return true
      }
      return ((this.data.scroll + this.domel.bodyh * 0.6) > this.domel['page' + n + 'top']) && ((this.data.scroll + this.domel.bodyh * 0.32) < this.domel['page' + n + 'btm'])
    },
    move (i) {
      if (this.switch) {
        this.switch = false
        let el = document.querySelector('#cphotosbox')
        if (i === -1 || i === -2) {
          this.isJump = true
          this.current += 6
          i += 6
          el.style.transform = 'translateX(' + this.current * (-360) + 'px)'
        } else if (i === 8 || i === 9) {
          this.isJump = true
          // console.log(this.isJump)
          this.current -= 6
          i -= 6
          el.style.transform = 'translateX(' + this.current * (-360) + 'px)'
        }
        // console.log(this.isJump)
        setTimeout(() => {
          this.isJump = false
          el.style.transform = 'translateX(' + i * (-360) + 'px)'
          setTimeout(() => {
            this.switch = true
          }, 900)
        }, 10)
        this.current = i
      }
    },
    playVideo (bool, n) {
      let time
      this['isVideoClass' + n] = bool
      if (bool) {
        time = 0
      } else {
        time = 600
      }
      setTimeout(() => {
        this.data.isAlert = bool
        this['isVideo' + n] = bool
        console.log(this.data.isAlert)
      }, time)
    },
    toBrand (b) {
      this.brand = b
      let el
      setTimeout(() => {
        if (b === 'c') {
          el = document.querySelector('#cPhotos')
          this.domel.page3ctop = el.offsetTop
          this.domel.page3cbtm = el.offsetTop + el.clientHeight
        } else if (b === 'm') {

        } else if (b === 'y') {

        }
      }, 20)
    }
  },
  props: ['data'],
  mounted () {
    // 只是等待元素加载完后能够正确获取到元素的宽高
    setTimeout(() => {
      let h = document.querySelector('body')
      let page1 = document.querySelector('#banner')
      let page2 = document.querySelector('#product')
      // let page3 = document.querySelector('#website')
      this.domel.bodyh = h.clientHeight
      this.domel.page1top = page1.offsetTop
      this.domel.page1btm = page1.offsetTop + page1.clientHeight
      this.domel.page2top = page2.offsetTop
      this.domel.page2btm = page2.offsetTop + page2.clientHeight
      // console.log(this.domel.page2top + '....' + this.domel.page2btm + '...' + page2.clientHeight)
    }, 10)
  }
}
</script>

